body {
	padding: 64px 0;
	background: #f1f1f1;
}

.color-card {
	margin-bottom: 30px;
}
.color-card-header {
	font-size: 1.3rem;
	margin-bottom: 4px;
}

.color-gal-row {
	margin: 0 -4px;
}
.color-gal-col {
	height: 52px;
	border-radius: 4px;
}

.app-header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 998;
	width: 100%;
	height: 50px;
	text-align: center;
	background: rgba(255, 255, 255, 0.9);
	backdrop-filter: blur(5px);
	border-bottom: 1px solid #ddd;
}
.app-header-overlay {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 999;
	width: 100%;
	height: 52px;
	padding: 6px 10px;
}
.app-header-title {
	width: 100%;
	font-size: 1.15rem;
	line-height: 1.1;
	font-weight: bold;
	margin: 0;
	padding: 0;
}
.app-header-title p {
	font-weight: normal;
	font-size: 0.7rem;
	padding: 1px 0 0;
	margin: 0;
	color: #9e9e9e;
}

.app-menu {
	padding: 2px 17px;
	position: fixed;
	z-index: 999;
	bottom: 0;
	left: 0;
	right: 0;
	background: rgba(255, 255, 255, 0.85);
	backdrop-filter: blur(5px);
	border-top: 1px solid #c0c0c0;
}

.app-menu-btn {
	padding: 5px;
	font-size: 0.6rem;
	color: #555;
}
.app-menu-btn i {
	margin-bottom: 2px;
	font-size: 1.5rem;
	color: #9e9e9e;
}

.app-menu-btn.active,
.app-menu-btn.active i {
	color: var(--primary);
}

/* ===== СТРАНИЦА УСТРОЙСТВ ===== */
.devices-btn {
	/* height: 70px; */
	margin-bottom: 10px;
	border-radius: 6px;
	/* text-align: center; */
	padding: 5px 10px;
	font-size: 0.8rem;
	background: #ffffff;
	box-shadow: 0 2px 10px #ddd;
	position: relative;
	overflow: hidden;
}
.devices-btn.active {
	box-shadow: 0 0 0 3px rgba(76, 175, 80, 0.41);
	background-color: #f7fbf3;
}
.devices-btn h6 {
	font-size: 1rem;
	font-weight: bold;
	margin-bottom: 0;
}
.devices-btn p {
	margin-bottom: 0;
}
.devices-btn p span {
	margin-right: 5px;
}

.device-status {
	display: inline-block;
	vertical-align: middle;
	width: 6px;
	height: 6px;
	background-color: #bfbfbf;
	border-radius: 50%;
	margin: 0 4px;
}
.devices-btn.status-1 .device-status {
	background-color: #28a745;
}
.devices-btn.status-0 .device-status {
	background-color: #dc3545;
}

/* ===== СТРАНИЦА ЭФФЕКТОВ ===== */
.effect-btn {
	height: 70px;
	margin-bottom: 10px;
	border-radius: 6px;
	text-align: center;
	padding: 5px;
	font-size: 0.9rem;
	background: #ffffff;
	box-shadow: 0 2px 10px #ddd;
	position: relative;
	overflow: hidden;
	cursor: pointer;
}
.effect-btn.active {
	box-shadow: 0 0 0 3px rgba(76, 175, 80, 0.41);
}
.effect-btn span {
	width: 100%;
	line-height: 1.2;
}

.effect-btn span.icon {
	position: absolute;
	bottom: -32%;
	right: -32%;
	font-size: 5rem;
	opacity: 0.25;
}

.effect-types {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	padding: 5px;
	text-align: left;
	line-height: 1;
}
.effect-types span {
	display: inline-block;
	vertical-align: top;
	width: 6px;
	height: 6px;
	background: #e6e5e5;
	border-radius: 50%;
	margin-right: 2px;
}
.effect-btn.active .effect-types span.active {
	background: #b2d6b1;
}

.effect-settings {
	position: fixed;
	bottom: 91px;
	left: 6px;
	right: 6px;
	height: auto;
	padding: 15px 15px 5px;
	background: #ffffff;
	border-radius: 15px;
	box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
	border: 2px solid #ccc;
}

.effect-settings-btn {
	position: fixed;
	bottom: 40px;
	left: 50%;
	width: 38px;
	height: 38px;
	text-align: center;
	line-height: 34px;
	background: #fff;
	font-size: 1.3rem;
	border-radius: 6px;
	border: 2px solid #ccc;
	transform: translate3d(-50%, -50%, 0);
	z-index: 99999;
}

.effect-settings-label {
	font-weight: bold;
	font-size: 0.9rem;
	line-height: 1.2;
	margin-bottom: -7px;
}

.rangeslider {
	margin: 20px 0;
	position: relative;
	background: #e6e6e6;
	touch-action: none;
}
.rangeslider-horizontal {
	height: 6px;
	border-radius: 10px;
}
.rangeslider-horizontal .rangeslider__fill {
	height: 100%;
	background-color: #acceff;
	border-radius: 10px;
	top: 0;
}
.rangeslider-horizontal .rangeslider__handle {
	width: 22px;
	height: 30px;
	border-radius: 30px;
	top: 50%;
	transform: translate3d(-50%, -50%, 0);
}
.rangeslider .rangeslider__handle {
	background: #fff;
	cursor: pointer;
	display: inline-block;
	position: absolute;
	border: 2px solid #027bff;
}
