.palette {
	width: 100%;
	margin-bottom: 30px;
}

.palette-line-color,
.palette-line-level {
	position: relative;
	width: 100%;
	height: 42px;
	border-radius: 6px;
	background: #dddddd;
}

.palette-line-color {
	background: -webkit-gradient(
		linear,
		left top,
		right top,
		from(red),
		color-stop(17%, #ff0),
		color-stop(33%, lime),
		color-stop(50%, cyan),
		color-stop(66%, blue),
		color-stop(83%, #f0f),
		to(red)
	);
	background: linear-gradient(
		to right,
		red 0%,
		#ff0 17%,
		lime 33%,
		cyan 50%,
		blue 66%,
		#f0f 83%,
		red 100%
	);
}

.palette-line-level,
.palette-line-color {
	&:before,
	& .before {
		content: " ";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border-radius: 5px;
	}
}

.palette-line-color .before {
	background: none;
}
.palette-line-level:before {
	background: linear-gradient(
		to right,
		#000 0%,
		rgba(0, 0, 0, 0) 50%,
		#fff 100%
	);
}

.palette-pointer {
	position: absolute;
	top: -3px;
	bottom: -3px;
	left: 0;
	width: 24px;
	transform: translate(-12px);
	background: #ffffff;
	border: 3px solid #fff;
	border-radius: 4px;
	box-shadow: 0 1px 4px rgba(0, 0, 0, 0.8);
	cursor: col-resize;
}
